'use client';

import React, { useState, useEffect } from "react";
import { Settings, LogOut, Search } from "lucide-react";
import { useRouter } from "next/navigation";

export default function Header() {
  const [currentTime, setCurrentTime] = useState("");
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);
  const router = useRouter();

  useEffect(() => {
    const interval = setInterval(() => {
      const now = new Date();
      const formattedTime = now.toLocaleString('en-GB', {
        weekday: 'short',
        day: 'numeric',
        month: 'short',
        year: 'numeric',
        hour: 'numeric',
        minute: 'numeric',
        second: 'numeric',
      });
      setCurrentTime(formattedTime);
    }, 1000);

    return () => clearInterval(interval);
  }, []);

  const handleLogout = () => {
    localStorage.removeItem("token");
    router.push("/auth/login");
  };

  const handleSettings = () => {
    router.push('/main/settings');
  };

  return (
    <div className="w-full bg-white">
      <div className="max-w-[1920px] mx-auto px-6 py-3">
        <div className="flex items-center justify-between">
          {/* Left side - Dashboard text */}
          <div className="flex-none">
            <h1 className="text-[#565DBD] text-base font-medium">
              Dashboard
            </h1>
          </div>

          {/* Right side - Search, DateTime, Profile */}
          <div className="flex items-center gap-6">
            {/* Search Bar */}
            <div className="relative">
              <div className="absolute inset-y-0 left-3 flex items-center pointer-events-none">
                <Search size={18} className="text-gray-400" />
              </div>
              <input
                type="text"
                placeholder="Search"
                className="w-80 pl-10 pr-4 py-2 bg-[#F4F3FF] rounded-lg text-sm placeholder-gray-400 focus:outline-none focus:ring-1 focus:ring-[#5D56BD]"
              />
            </div>

            {/* DateTime */}
            <span className="text-sm text-gray-600 whitespace-nowrap">
              {currentTime}
            </span>

            {/* Profile Dropdown */}
            <div className="relative">
              <div 
                className="flex items-center gap-2 bg-[#F4F3FF] rounded-full pr-2 cursor-pointer hover:bg-[#ECEAFF] transition-colors"
                onClick={() => setIsDropdownOpen(!isDropdownOpen)}
              >
                <div className="w-9 h-9 rounded-full overflow-hidden">
                  <img
                    src="https://www.creativefabrica.com/wp-content/uploads/2023/02/09/Flat-People-Icon-Graphics-60534092-1.png"
                    alt="Profile"
                    className="w-full h-full object-cover"
                  />
                </div>
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className={`w-4 h-4 transition-transform duration-200 ${
                    isDropdownOpen ? 'rotate-180 text-[#5D56BD]' : 'text-gray-600'
                  }`}
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  strokeWidth="2"
                >
                  <path strokeLinecap="round" strokeLinejoin="round" d="M19 9l-7 7-7-7" />
                </svg>
              </div>

              {/* Dropdown Menu */}
              {isDropdownOpen && (
                <div className="absolute right-0 mt-2 w-48 bg-white rounded-lg shadow-lg py-1 z-50">
                  <button 
                    className="w-full px-4 py-2 text-sm text-gray-700 hover:bg-gray-50 flex items-center"
                    onClick={handleSettings}
                  >
                    <Settings className="w-4 h-4 mr-2" />
                    Settings
                  </button>
                  <button
                    className="w-full px-4 py-2 text-sm text-gray-700 hover:bg-gray-50 flex items-center"
                    onClick={handleLogout}
                  >
                    <LogOut className="w-4 h-4 mr-2"/>
                    Logout
                  </button>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}