import React, { useState } from "react";
import { ChevronLeft, ChevronRight } from "lucide-react";

export default function Schedule() {
  const today = new Date();
  const [currentMonth, setCurrentMonth] = useState(today.getMonth());
  const [currentYear, setCurrentYear] = useState(today.getFullYear());
  const [currentDate, setCurrentDate] = useState(today.getDate());
  
  // Store the actual current month and year for comparison
  const actualMonth = today.getMonth();
  const actualYear = today.getFullYear();

  const getDaysForMonth = () => {
    const firstDay = new Date(currentYear, currentMonth, 1).getDay();
    const firstDayIndex = firstDay === 0 ? 6 : firstDay - 1;

    const daysInMonth = new Date(currentYear, currentMonth + 1, 0).getDate();
    const daysInPrevMonth = new Date(currentYear, currentMonth, 0).getDate();

    const days = [];

    // Previous month's days
    for (let i = firstDayIndex - 1; i >= 0; i--) {
      days.push({
        day: daysInPrevMonth - i,
        isCurrentMonth: false,
        month: currentMonth - 1,
        year: currentYear,
      });
    }

    // Current month's days
    for (let day = 1; day <= daysInMonth; day++) {
      days.push({
        day,
        isCurrentMonth: true,
        month: currentMonth,
        year: currentYear,
      });
    }

    // Next month's days
    const remainingDays = 42 - days.length;
    for (let day = 1; day <= remainingDays; day++) {
      days.push({
        day,
        isCurrentMonth: false,
        month: currentMonth + 1,
        year: currentYear,
      });
    }

    return days;
  };

  const navigateMonth = (direction: number) => {
    if (direction === -1 && currentMonth === 0) {
      setCurrentMonth(11);
      setCurrentYear(currentYear - 1);
    } else if (direction === 1 && currentMonth === 11) {
      setCurrentMonth(0);
      setCurrentYear(currentYear + 1);
    } else {
      setCurrentMonth(currentMonth + direction);
    }
  };

  const months = [
    "January", "February", "March", "April", "May", "June",
    "July", "August", "September", "October", "November", "December"
  ];

  const weekdays = ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"];
  const formatDate = () => {
    const date = new Date();
    return date.toLocaleDateString('en-US', { 
      weekday: 'short', 
      month: 'short', 
      day: 'numeric' 
    });
  };
  

  return (
    <div className=" max-w-100 p-6 bg-white rounded-lg">
      <div className="flex items-center justify-between mb-4">
      </div>

      <h2 className="text-2xl font-semibold text-gray-800 mb-6">
        {formatDate()}
      </h2>
      <hr />
      <div className="flex items-center justify-between mb-4">
        <button
          className="flex items-center text-gray-600 hover:text-gray-800"
          onClick={() => {
            const newDate = new Date(currentYear, currentMonth);
            return newDate.toLocaleDateString('en-US', { 
              month: 'long',
              year: 'numeric'
            });
          }}
        >
          {months[currentMonth]} {currentYear}
        </button>
        <div className="flex gap-4">
          <button 
            onClick={() => navigateMonth(-1)}
            className="p-1 text-gray-600 hover:text-gray-800"
          >
            <ChevronLeft size={20} />
          </button>
          <button 
            onClick={() => navigateMonth(1)}
            className="p-1 text-gray-600 hover:text-gray-800"
          >
            <ChevronRight size={20} />
          </button>
        </div>
      </div>

      <div className="grid grid-cols-7 gap-1">
        {weekdays.map((day) => (
          <div key={day} className="text-center text-sm text-gray-500 py-2">
            {day}
          </div>
        ))}

        {getDaysForMonth().map((dayInfo, index) => {
          const isCurrentDay = 
            dayInfo.day === currentDate &&
            dayInfo.isCurrentMonth &&
            dayInfo.month === actualMonth &&
            dayInfo.year === actualYear;

          return (
            <div
              key={index}
              className={`
                relative flex items-center justify-center h-10
                ${!dayInfo.isCurrentMonth ? 'text-gray-400' : 'text-gray-900'}
                ${isCurrentDay ? 'bg-indigo-100 rounded-full font-semibold' : ''}
              `}
            >
              <span className="relative z-10">{dayInfo.day}</span>
            </div>
          );
        })}
      </div>
    </div>
  );
}